@import "../../../../styles/abstracts/mixins";

.checkbox {
  &__label {
    display: flex;
    align-items: center;
    gap: toRem(8);

    color: var(--gray-500);
  }

  &__input-container {
    position: relative;
    display: flex;

    svg {
      position: absolute;
      height: toRem(16);
      width: toRem(16);
      stroke: var(--white);
    }
  }

  &__input {
    appearance: none;

    &::before {
      content: "";
      display: inline-block;
      width: toRem(16);
      height: toRem(16);
      border: toRem(1) solid var(--gray-200);
      border-radius: toRem(4);
      background-color: var(--white);
    }

    &:checked::before {
      background-color: var(--red-600);
      border: toRem(1) solid var(--red-600);
    }
  }
}
